import s from "../style/accountForm.module.sass"
import { useIdentity } from "../hooks/identity";
import { DropdownItem } from '../components/MultilevelMenu';
import { FaPlus, FaCheck, FaSkull } from "react-icons/fa";

export function AccountPicker() {
    var [id, axios] = useIdentity();

    return <>
        {id.users.map((user, idx)=> {
            return (
            <DropdownItem 
                text={user.username} 
                rightIcon={idx === id.active ? 
                    <FaCheck/> 
                    : undefined} 
                key = {idx}
                onClick={() => {
                    if (id.active === idx) {
                        return id.setActive(null);
                    }       
                    id.setActive(idx)
                }}/>
        );})}
        <DropdownItem leftIcon={<FaPlus/>} text="Add Acount"/>
    </>;
    
}
